
body {
  background-color: rgba(28,29,33,1);
  color: rgba(255,255,255,1);
  font-family: "Open Sans", sans-serif;
  line-height: 1.45em;
  margin: 1em;
}
code {
  background-color: #eee;
  display: inline-block;
  font-family: 'Inconsolata', monospace;
  padding: 0.25em 1em;
}
.masthead {
  -webkit-box-align: baseline;
      -ms-flex-align: baseline;
          align-items: baseline;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  margin-bottom: 1em;
}
.masthead h1 {
  font-size: 24px;
  line-height: 1.45em;
}
form {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
label {
  display: block;
}
input {
  margin-right: 1em;
}
button {
  -moz-appearance: none;
  -webkit-appearance: none;
  background-color: transparent;
  border: 0;
  display: inline-block;
  -ms-flex-negative: 0;
      flex-shrink: 0;
  margin: 1em 1em 0 0;
}
.button {
  border: 1px solid rgba(255, 255, 255, 1);
  border-radius: 4px;
  color: rgba(255, 255, 255, 1);
  cursor: pointer;
  padding: 0.5em 1em;
}



